/**
 * @desc : Recoil Atoms 
 */

import { atom, selector } from "recoil";

// [enum] string   
export enum Categories {
  "TODO" = "TODO",
  "ACTIVE" = "ACTIVE",
  "DONE" = "DONE",
}
export interface IToDo {
  text: string;
  id: number;
  category: Categories;
}

// [Atom] todo
export const todoState = atom<IToDo[]>({
  key: "toDo",
  default: [],
  // localStorage  
  effects: [
    ({ setSelf, onSet }) => {
      const localKey = "Todo";
      const savedValue = localStorage.getItem(localKey);
      if (savedValue !== null) {
        setSelf(JSON.parse(savedValue));
      }

      onSet((newValue, _, isReset) => {
        isReset
          ? localStorage.removeItem(localKey)
          : localStorage.setItem(localKey, JSON.stringify(newValue));
      });
    },
  ],
});

// [Atom] category
export const categoryState = atom<Categories>({
  key: "category",
  default: Categories.TODO,
});

// Selector
export const toDoSelector = selector({
  key: "toDoSelector",
  get: ({ get }) => {
    // selector atom   get .
    const toDos = get(todoState);
    const category = get(categoryState);

    // 3.2  
    return toDos.filter((todo) => todo.category === category);

    // 2.    
    // if (category === "TODO")
    //   return toDos.filter((todo) => todo.category === "TODO");
    // if (category === "ACTIVE")
    //   return toDos.filter((todo) => todo.category === "ACTIVE");
    // if (category === "DONE")
    //   return toDos.filter((todo) => todo.category === "DONE");

    // 1.     
    // return [
    //   toDos.filter((todo) => todo.category === "TODO"),
    //   toDos.filter((todo) => todo.category === "ACTIVE"),
    //   toDos.filter((todo) => todo.category === "DONE"),
    // ];
  },
});
